<template>
    <div class="content">
        <div class="category">
            <div
            :class="{'category__item': true, 'category__item--active': currentTab === item.tab}"
            v-for="item in categories"
            :key="item.name"
            @click="() => handleTabClick(item.tab)"
            >
            {{item.name}}
            </div>
            <!-- <div class="category__item category__item--active">全部商品</div> -->
        </div>
        <div class="product">
            <div class="product__item"
            v-for="item in list"
            :key="item._id"
            >
                <img src="http://www.dell-lee.com/imgs/vue3/near.png" alt="" srcset="" class="product__item__img">
                <div class="product__item__detail">
                    <h4 class="product__item__title ellipsis">{{item.name}}</h4>
                    <p class="product__item__sales">月售{{item.sales}}件</p>
                    <div class="product__item__price">
                         <span class="product__item__yen">&yen;</span>{{item.price}}
                         <span class="product__item__origin">&yen;{{item.oldPrice}}</span>
                    </div>
                </div>
                <div class="product__number">
                    <span
                    class="product__number__minus"
                    @click="() => { changeCartItem(shopId, item._id, item, -1, shopName)}"
                    >-</span>
                        <!-- {{getProductCartCount(shopId, item._id)}} -->
                    <!-- {{cartList?.[shopId]?.[item._id]?.count || 0}} -->
                    {{cartList?.[shopId]?.productList?.[item._id]?.count || 0}}
                    <span
                    class="product__number__plus"
                    @click="() => { changeCartItem(shopId, item._id, item, 1, shopName)}"
                    >+</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { reactive, toRefs, ref, watchEffect } from 'vue'
import { get } from '../../utils/request'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { useCommonCartEffect } from './commonCartEffect'

const categories = [
  { name: '全部商品', tab: 'all' },
  { name: '秒杀', tab: 'seckill' },
  { name: '新鲜水果', tab: 'fruit' },
  { name: '热销', tab: 'hot' },
  { name: '水果1', tab: 'fruit1' },
  { name: '秒杀1', tab: 'seckill1' },
  { name: '新鲜水果2', tab: 'fruit2' },
  { name: '秒杀2', tab: 'seckill2' },
  { name: '新鲜水果3', tab: 'fruit3' }
]

const useTabEffect = () => {
  const currentTab = ref(categories[0].tab)
  const handleTabClick = (tab) => {
    currentTab.value = tab
  }
  //  const handleTabClick = (tab) => {
  //       currentTab.value = tab
  //     }
  return { currentTab, handleTabClick }
}

const useCurrentListEffect = (currentTab) => {
  const route = useRoute()
  const shopId = route.params.id
  const content = reactive({ list: [] })
  const getContentData = async () => {
    const result = await get(`/api/shop/${shopId}/products`, { tab: currentTab.tab })
    if (result?.code === 200 && result?.data?.length) {
      content.list = result.data
    }
  }
  watchEffect(() => { getContentData() })
  const { list } = toRefs(content)
  return { list }
}

// 购物车相关逻辑
const useCartEffect = () => {
  const store = useStore()
  const { cartList, changeCartItemInfo } = useCommonCartEffect()
  const changeShopName = (shopId, shopName) => {
    store.commit('changeShopName', { shopId, shopName })
  }
  const changeCartItem = (shopId, productId, item, num, shopName) => {
    console.log(shopName)
    changeCartItemInfo(shopId, productId, item, num)
    changeShopName(shopId, shopName)
  }
  const getProductCartCount = (shopId, productId) => {
    console.log('productId=' + productId)
    return cartList?.[shopId]?.productList?.[productId]?.count || 0
  }
  return { cartList, changeCartItem, getProductCartCount }
}

export default {
  name: 'Content',
  props: ['shopName'],
  setup () {
    const route = useRoute()
    const shopId = route.params.id
    const { currentTab, handleTabClick } = useTabEffect()
    const { list } = useCurrentListEffect(currentTab)
    // const { cartList, changeCartItemInfo } = useCommonCartEffect()
    const { changeCartItem, cartList, getProductCartCount } = useCartEffect()
    return {
      categories,
      currentTab,
      handleTabClick,
      list,
      shopId,
      changeCartItem,
      cartList,
      getProductCartCount
    }
    // return { categories, currentTab, handleTabClick, list, cartList, changeCartItemInfo, shopId }
    // const data = reactive({
    //   currentTab: categories[0].tab,
    //   contentList: []
    // })
    // const getContentData = async () => {
    //   const result = await get('/api/shop/1/products')
    //   if (result?.code === 200 && result?.data?.length) {
    //     data.contentList = result.data
    //   }
    // }
    // const handleCategoryClick = (tab) => {
    //   getContentData(tab)
    //   data.currentTab = tab
    // }
    // getContentData('all')
    // const { contentList, currentTab } = toRefs(data)
    // return { categories, currentTab, contentList, handleCategoryClick }
  }
}
</script>

